function Toggle() {
    const body = document.body;
    body.className = "Theme-1"


    const changeTheme = (e) => {
        body.className = e.target.value
    }
    return (
        <div className="radio-wrapper">
            <input type="radio" name="theme" value="Theme-1" className="theme-1" id="radio-1" onChange={changeTheme} />
            <label for="radio-1">1</label>

            <input type="radio" name="theme" value="Theme-2" className="theme-2" id="radio-2" onChange={changeTheme} />
            <label for="radio-2">2</label>

            <input type="radio" name="theme" value="Theme-3" className="theme-3" id="radio-3" onChange={changeTheme} />
            <label for="radio-3">3</label>

            <div className="selector">&nbsp;</div>

        </div>)
}

export default Toggle;